html {
  --main-color: black;
  --highlight-color: midnightblue;
  --hover-color: dodgerblue;
  --background-color: white;
}

header, main {
  width: 60%;
  min-width: 700px;
  max-width: 1000px;
  margin: auto;
}

.body-header {
  justify-content: space-between;
  margin-top: 2em;
  display: flex;
}

.header-title-employee-name, .header-title-job-position {
  text-align: center;
  color: var(--highlight-color);
  margin-top: 0;
  margin-bottom: 0;
  position: relative;
}

.header-title-job-position {
  font-size: medium;
}

.title-contacts, .header-title {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.contacts-links-margin-top {
  margin-top: .5em;
}

.title-contacts-contact {
  font-size: small;
}

.title-contacts-email, .title-contacts-divider, .title-contacts-website {
  display: none;
}

.description-title {
  color: var(--highlight-color);
  background-color: var(--background-color);
  z-index: 2;
  width: -moz-fit-content;
  width: fit-content;
  margin-bottom: 0;
  margin-left: auto;
  margin-right: auto;
  padding: 0 5%;
  position: relative;
}

.description-title-container {
  text-align: center;
  position: relative;
}

.description-title-container:after {
  content: "";
  border-top: 1px solid var(--highlight-color);
  z-index: 1;
  width: 100%;
  position: absolute;
  top: 50%;
  left: 0;
}

.description-summary-title {
  color: var(--highlight-color);
  cursor: pointer;
  margin-top: 1.3em;
}

.description-summary-title:hover {
  color: var(--hover-color);
}

a {
  color: var(--highlight-color);
  font-style: italic;
  text-decoration: none;
  transition: all .3s linear;
}

a:hover {
  color: var(--hover-color);
}

.history-dates {
  font-size: smaller;
}

.history-tech-stack, .history-faculty-title {
  margin-top: 0;
  margin-bottom: 0;
}

.history-title-without-margin {
  margin-bottom: 10px;
}

.contacts-links svg {
  width: 20px;
}

a:not(:hover) svg path {
  fill: var(--highlight-color);
  transition: all .6s linear;
}

a:hover svg path {
  transition: all .3s linear;
}

.contacts-links {
  justify-content: center;
  align-items: center;
  display: flex;
}

.contacts-link-link {
  margin-right: 10px;
}

.achivements-item {
  padding-bottom: 5px;
}

.aside-content-download-button {
  position: fixed;
  bottom: 30px;
  right: 30px;
}

.aside-content-download-icon {
  width: 50px;
  height: 50px;
}

.aside-content-tooltip {
  background-color: var(--highlight-color);
  color: #fff;
  border-radius: 5px;
  max-width: 250px;
  padding: 5px;
  position: absolute;
}

.history-tech-stack span:not(:first-child) {
  cursor: pointer;
  -webkit-text-decoration: underline dashed;
  text-decoration: underline dashed;
}

.history-tech-stack span:not(:first-child):hover {
  color: var(--hover-color);
}

.language-button {
  cursor: pointer;
  background: none;
  border: none;
  width: 50px;
  height: 50px;
  position: fixed;
  bottom: 90px;
  right: 30px;
}

.tech-stack-item {
  display: inline-block;
  position: relative;
}

.tech-stack-item .tooltip {
  background-color: var(--hover-color);
  color: var(--main-color);
  text-align: center;
  z-index: 1;
  border-radius: 6px;
  width: 16vw;
  min-width: 200px;
  padding: 10px;
  display: none;
  position: absolute;
  bottom: calc(100% + 10px);
  left: 50%;
  transform: translateX(-50%);
}

.tech-stack-item:hover .tooltip, .tech-stack-item:focus .tooltip {
  display: unset;
}

